.navbar-brand {
    display: fixed;
    -ms-flex-align: center;
        align-items: center;

  }
  .navbar-brand>img {
    padding: 7px 14px;
  }

  .nav-wrapper {
    background: -webkit-gradient(linear, left top, right top, from(#29bcd3), to(black));
    background: -webkit-linear-gradient(left, #29bcd3, black);
    background: -o-linear-gradient(left, #29bcd3, black);
    background: linear-gradient(to right, #29bcd3, black);
  }

  .nav-wrapper > ul > li > a {
    font-size: 1.5em;
    text-transform: uppercase;
    color: white; 
    font-family: GillSans, Calibri, Trebuchet, sans-serif;
  }

  .nav-wrapper > ul > li > a:hover {
    text-transform: uppercase;
    background-color: rgb(26, 81, 95);
    color: white; 
    font-family: GillSans, Calibri, Trebuchet, sans-serif;
    color: #ffffff; 
    -webkit-transition: all 0.7s; 
    -o-transition: all 0.7s; 
    transition: all 0.7s;
    -webkit-transform: scale(1.3);
        -ms-transform: scale(1.3);
            transform: scale(1.3);
    border-radius: 20px;
  }


