
.fish_wrap2 {
    height: 230px;
    left: 1000px;
    position: absolute;
    top: 305px;
    width: 355px;
    z-index: 90;
}

.fish2 {
    -webkit-transform: rotate(-37deg);
        -ms-transform: rotate(-37deg);
            transform: rotate(-37deg);
}

.fish_body2 {
    -webkit-transform: skewX(30deg) skewY(5deg);
        -ms-transform: skewX(30deg) skewY(5deg);
            transform: skewX(30deg) skewY(5deg);
    background-image: -webkit-radial-gradient(rgb(var(--colorOneR),var(--colorOneG),var(--colorOneB)) var(--degree), rgb(var(--colorTwoR),var(--colorTwoG),var(--colorTwoB)) 2px);
    background-image: -o-radial-gradient(rgb(var(--colorOneR),var(--colorOneG),var(--colorOneB)) var(--degree), rgb(var(--colorTwoR),var(--colorTwoG),var(--colorTwoB)) 2px);
    background-image: radial-gradient(rgb(var(--colorOneR),var(--colorOneG),var(--colorOneB)) var(--degree), rgb(var(--colorTwoR),var(--colorTwoG),var(--colorTwoB)) 2px);
    background-size:40px 40px;
    border-radius: 1500px 1200px 757px 553px; /*orig*/
    -webkit-box-shadow: 6px -12px 15px #3A2104 inset, -2px -3px 4px #281602 inset;
            box-shadow: 6px -12px 15px #3A2104 inset, -2px -3px 4px #281602 inset;
    height: 144px;
    position: absolute;
    top: 260px;
    width: 184px;
}

.eye2 {
    -webkit-transform: rotate(50deg);
        -ms-transform: rotate(50deg);
            transform: rotate(50deg);
    background-color: rgb(216, 214, 214);
    border: 1px solid #323232;
    border-radius: 10px 10px 10px 10px;
    -webkit-box-shadow: 2px 4px 3px #3D270A;
            box-shadow: 2px 4px 3px #3D270A;
    height: 27px;
    left: 80px;
    position: absolute;
    top: 10px;
    width: 10px;
}

.eye2:before {
    /*fish eye pupil*/
    border: 2px solid #01090a;
    border-radius: 10px 10px 10px 10px;
    content: "";
    height: 7px;
    left: 1px;
    position: absolute;
    top: 11px;
    width: 2px;
}

.scale_1_2 {
    -webkit-transform: rotate(35deg);
        -ms-transform: rotate(35deg);
            transform: rotate(35deg);
    border-radius: 60px 60px 60px 60px;
    border-right: 2px solid #432801;
    height: 28px;
    left: 68px;
    position: absolute;
    top: 8px;
    width: 36px;
}

.scale_2_2 {
    -webkit-transform: rotate(35deg);
        -ms-transform: rotate(35deg);
            transform: rotate(35deg);
    border-radius: 60px 60px 60px 60px;
    border-right: 2px solid #432801;
    height: 28px;
    left: 60px;
    position: absolute;
    top: 27px;
    width: 36px;
}

.scale_3_2 {
    -webkit-transform: rotate(35deg);
        -ms-transform: rotate(35deg);
            transform: rotate(35deg);    
    border-radius: 60px 60px 60px 60px;   
    border-right: 2px solid #432801;
    height: 28px;
    left: 77px;
    position: absolute;
    top: 19px;
    width: 36px;
}

.scale_4_2 {
    -webkit-transform: rotate(35deg);
        -ms-transform: rotate(35deg);
            transform: rotate(35deg);    
    border-radius: 60px 60px 60px 60px;    
    border-right: 2px solid #432801;
    -webkit-box-shadow: -5px -1px 4px black inset;
            box-shadow: -5px -1px 4px black inset;    
    height: 32px;
    left: 63px;
    position: absolute;
    top: 29px;
    width: 19px;
}

.tail_fin2 {
    -webkit-transform: skewX(30deg) skewY(5deg);
        -ms-transform: skewX(30deg) skewY(5deg);
            transform: skewX(30deg) skewY(5deg);    
    background-color: rgb(var(--colorTwoR), var(--colorTwoG), var(--colorTwoB));
    border-radius: 200px 112px 47px 62px;    
    -webkit-box-shadow: -15px -7px 9px #000604 inset;    
            box-shadow: -15px -7px 9px #000604 inset;    
    height: 32px;
    left: 191px;
    position: absolute;
    top: 381px;
    width: 42px;
}

.tail_fin2:before {
    -webkit-transform: skewX(30deg) skewY(5deg);
        -ms-transform: skewX(30deg) skewY(5deg);
            transform: skewX(30deg) skewY(5deg);   
    background-color: rgb(var(--colorTwoR), var(--colorTwoG), var(--colorTwoB));
    border-radius: 62px 112px 112px 200px;
    -webkit-box-shadow: 6px 13px 9px #2F1E01 inset, -6px -7px 2px black inset;
            box-shadow: 6px 13px 9px #2F1E01 inset, -6px -7px 2px black inset;
    content: "";
    height: 32px;
    left: -12px;
    position: absolute;
    top: 28px;
    width: 38px;
}

/* start of fish animation */

.fish_wrap2 {
    left: 200px;
    top: 105px;
    z-index: 100;
    -webkit-animation: swim2 var(--animation-time) ease infinite;
            animation: swim2 var(--animation-time) ease infinite;
    /* animation-name: swim2;
    animation-duration: 15s;
    animation-iteration-count: infinite; */
 }

@-webkit-keyframes swim2 {
    from {
        -webkit-transform: scale3d(1);
                transform: scale3d(1);
        left: 500px;
        top:-175px;
    }
    5% {
        left: 488px;
    }
    12% {
        left: 488px;
        top: -190px;
    }
    22% {
        left: 425px;
        top: -125px;
    }
    28% {
        left: 407px;
        top: -95px;
    }
    33% {
        -webkit-transform: scale3d(-1);
                transform: scale3d(-1);
        left: 802px;
        top: -50px;
    }
    45% {
        left:802px;
        top: 0px;
    }
    48% {
        top: 25px;
    }
    49% {
        top: 43px;
    }
    55% {
        left: 625px;
        top: 75px;
    }
    68% {
        left: 555px;
    }

    75% {
        left: 500px;
        top: 25px;
    }
    83% {
        top: -25px;
    }
    93% {
        top: -155px;
    }
    97% {
        top: -168px;
    }
    to {
        -webkit-transform: scaleX(1);
                transform: scaleX(1);
        left: 500px;
        top: -175px;
        z-index: 90;
    }
}

@keyframes swim2 {
    from {
        -webkit-transform: scale3d(1);
                transform: scale3d(1);
        left: 500px;
        top:-175px;
    }
    5% {
        left: 488px;
    }
    12% {
        left: 488px;
        top: -190px;
    }
    22% {
        left: 425px;
        top: -125px;
    }
    28% {
        left: 407px;
        top: -95px;
    }
    33% {
        -webkit-transform: scale3d(-1);
                transform: scale3d(-1);
        left: 802px;
        top: -50px;
    }
    45% {
        left:802px;
        top: 0px;
    }
    48% {
        top: 25px;
    }
    49% {
        top: 43px;
    }
    55% {
        left: 625px;
        top: 75px;
    }
    68% {
        left: 555px;
    }

    75% {
        left: 500px;
        top: 25px;
    }
    83% {
        top: -25px;
    }
    93% {
        top: -155px;
    }
    97% {
        top: -168px;
    }
    to {
        -webkit-transform: scaleX(1);
                transform: scaleX(1);
        left: 500px;
        top: -175px;
        z-index: 90;
    }
}

/*end of fish path */

.tail_fin2 {
    -webkit-animation-name: tail2;
            animation-name: tail2;
    -webkit-animation-duration: 0.9s;
            animation-duration: 0.9s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
}

@-webkit-keyframes tail2 {
    from {
        -webkit-transform: scale(.8)skewY(2deg);
                transform: scale(.8)skewY(2deg);
    }
    50% {
        -webkit-transform: scale(.7)skewY(20deg);
                transform: scale(.7)skewY(20deg);
    }
    to {
        -webkit-transform: scale(.8)skewY(2deg);
                transform: scale(.8)skewY(2deg);
    }
}

@keyframes tail2 {
    from {
        -webkit-transform: scale(.8)skewY(2deg);
                transform: scale(.8)skewY(2deg);
    }
    50% {
        -webkit-transform: scale(.7)skewY(20deg);
                transform: scale(.7)skewY(20deg);
    }
    to {
        -webkit-transform: scale(.8)skewY(2deg);
                transform: scale(.8)skewY(2deg);
    }
}
/*end of tail fin*/

.scale_4_2 {
    /*mid fin*/
    -webkit-animation-name: midfin2;
            animation-name: midfin2;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}

@-webkit-keyframes midfin2 {
    from {
        -webkit-transform: scale(.8)rotate(15deg);
                transform: scale(.8)rotate(15deg);
    }
    50% {
        -webkit-transform: scale(.6)skewY(30deg)rotate(15deg);
                transform: scale(.6)skewY(30deg)rotate(15deg);
    }
    80% {
        -webkit-transform: scale(.8)skewY(10deg)rotate(25deg);
                transform: scale(.8)skewY(10deg)rotate(25deg);
    }
    to {
        -webkit-transform: scale(.8)skewY(10deg)rotate(15deg);
                transform: scale(.8)skewY(10deg)rotate(15deg);
    }
}

@keyframes midfin2 {
    from {
        -webkit-transform: scale(.8)rotate(15deg);
                transform: scale(.8)rotate(15deg);
    }
    50% {
        -webkit-transform: scale(.6)skewY(30deg)rotate(15deg);
                transform: scale(.6)skewY(30deg)rotate(15deg);
    }
    80% {
        -webkit-transform: scale(.8)skewY(10deg)rotate(25deg);
                transform: scale(.8)skewY(10deg)rotate(25deg);
    }
    to {
        -webkit-transform: scale(.8)skewY(10deg)rotate(15deg);
                transform: scale(.8)skewY(10deg)rotate(15deg);
    }
}
/*end of mid fin*/

.eye2 {
    /*blink effect*/
    -webkit-animation-name: blink2;
            animation-name: blink2;
    -webkit-animation-duration: 5.7s;
            animation-duration: 5.7s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;   
}

@-webkit-keyframes blink2 {
    from {
        background-color: white;
    }
    49% {
        background-color: white;
        height: 27px;
        width: 10px;
    }
    50% {
        background-color: #070707;
        height: 20px;
        width: 5px;
    }
    52% {
        background-color: white;
        height: 27px;
        width: 10px;
    }
    to {
        background-color: white;
    }
}

@keyframes blink2 {
    from {
        background-color: white;
    }
    49% {
        background-color: white;
        height: 27px;
        width: 10px;
    }
    50% {
        background-color: #070707;
        height: 20px;
        width: 5px;
    }
    52% {
        background-color: white;
        height: 27px;
        width: 10px;
    }
    to {
        background-color: white;
    }
}





